























import { Component, Vue } from 'vue-property-decorator';
import { Switcher } from 'd3-fc-switcher'


@Component({
  name: 'Switcher'
})
export default class Home extends Vue {

  switcher: Switcher| undefined

  mounted(){
    this.switcher = Switcher.makeDefault()
  }
  beforeDestroy(){
    this.switcher && this.switcher.destroy()
  }
}
